:root {
  --primary: #2E7D32;
  --primary-light: #4CAF50;
  --primary-dark: #1B5E20;
  --secondary: #66BB6A;
  --accent: #F9A825;
  --accent-dark: #F57F17;
  --light-bg: #F1F8E9;
  --white: #ffffff;
  --dark: #1a1a2e;
  --gray: #6B7280;
  --light-gray: #F9FAFB;
  --font-heading: 'Poppins', sans-serif;
  --font-body: 'Inter', sans-serif;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: var(--font-body);
  color: var(--dark);
  background: #ffffff;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 3px;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse-green {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(46, 125, 50, 0.4);
  }
  50% {
    box-shadow: 0 0 0 15px rgba(46, 125, 50, 0);
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes countUp {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200% center;
  }
  100% {
    background-position: 200% center;
  }
}
.animate-fadeInUp {
  animation: fadeInUp 0.7s ease forwards;
}
.animate-fadeInLeft {
  animation: fadeInLeft 0.7s ease forwards;
}
.animate-fadeInRight {
  animation: fadeInRight 0.7s ease forwards;
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
.animate-pulse-green {
  animation: pulse-green 2s infinite;
}
.section-padding {
  padding: 90px 0;
}
.container-custom {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.section-badge {
  display: inline-block;
  background: rgba(46, 125, 50, 0.1);
  color: var(--primary);
  padding: 6px 18px;
  border-radius: 50px;
  font-size: 13px;
  font-weight: 600;
  font-family: var(--font-heading);
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 14px;
}
.section-title {
  font-family: var(--font-heading);
  font-size: clamp(28px, 4vw, 42px);
  font-weight: 700;
  color: var(--dark);
  line-height: 1.25;
  margin-bottom: 16px;
}
.section-title span {
  color: var(--primary);
}
.section-desc {
  font-family: var(--font-body);
  font-size: 16px;
  color: var(--gray);
  line-height: 1.8;
  max-width: 560px;
}
.btn-primary {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: var(--primary);
  color: white;
  padding: 14px 30px;
  border-radius: 50px;
  font-family: var(--font-heading);
  font-weight: 600;
  font-size: 15px;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
}
.btn-primary:hover {
  background: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(46, 125, 50, 0.35);
}
.btn-outline {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: transparent;
  color: white;
  padding: 14px 30px;
  border-radius: 50px;
  font-family: var(--font-heading);
  font-weight: 600;
  font-size: 15px;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  border: 2px solid rgba(255,255,255,0.6);
}
.btn-outline:hover {
  background: white;
  color: var(--primary);
  border-color: white;
  transform: translateY(-2px);
}
.card-hover {
  transition: all 0.3s ease;
}
.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.12);
}
.green-gradient {
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--primary) 50%, var(--secondary) 100%);
}
.divider {
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  border-radius: 2px;
  margin: 16px 0;
}
